import { BASE_URL } from '../consts';
import { ISuccessResponse } from '../services/types/authTypes';

async function checkResponse<T>(res: Response): Promise<T> {
	if (!res.ok) {
		const errorData = await res.json().catch(() => null);
		throw new Error(errorData?.message || ` ${res.status}`);
	}
	return res.json();
}

const checkSuccess = <T extends ISuccessResponse>(res: T): T => {
	if (res && res.success) {
		return res;
	}
	throw new Error(`  success: ${res}`);
};

export const request = <T extends ISuccessResponse>(
	endpoint: string,
	options?: RequestInit
): Promise<T> => {
	return fetch(`${BASE_URL}${endpoint}`, options)
		.then((res) => checkResponse<T>(res))
		.then((data) => checkSuccess<T>(data));
};
